.admin {
  &__title {
    display: flex;
    flex-direction: column;
  }
  &__subtitle {
    display: block;
    font-size: 22px;
    margin-top: 5px;
  }
  &__table {
    & .dataTables_wrapper .dataTables_paginate .paginate_button {
      background: none;
      border-radius: 5px;
      transition: all 0.4s;
      &:hover {
        background: #020202;
        color: #ffffff !important;
      }
    }
    & button.dt-button, div.dt-button, a.dt-button, input.dt-button {
      background: none;
      border-radius: 5px;
      transition: all 0.4s;
      &:hover {
        background: #22a019;
        color: #ffffff !important;
        border: 1px solid #22a019;
      }
    }
    & a {
      color: #FF4D1C;
      text-decoration: underline;
      &:hover {
        color: #020202;
      }
    }
    & .dataTables_info {
      display: none;
    }
    & td {
      border-bottom: 1px solid #aeaeae !important;
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;
      color: #333333;
      padding: 15px 10px;

      &:not(:last-child) {
        border-right: 1px solid #e1e1e1;
      }
    }
    & .dataTables_wrapper .dataTables_paginate {
      margin-top: 15px;
      float: none;
      text-align: center;
    }
    & thead {
      & td {
        font-weight: 500;
        font-size: 18px;
      }
    }
  }
}

.dataTables_wrapper .dataTables_filter {
  margin-bottom: 20px;
}

table.dataTable {
  margin: 0;
  width: 100% !important;
}

.dataTables_filter {
  position: relative;
  z-index: 2;
}

.admin {

  &__moderate {
    color: #103FE3 !important;
  }

  &__active {
    color: #39ac08 !important;
  }

  &__cancel {
    color: #FF0505 !important;
  }

  &-filter {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: -55px;

    &__item {
      display: flex;
      gap: 10px;

      & span {
        font-weight: 600;
        font-size: 16px;
      }
      & select {
        border: 1px solid #3344E5 !important;
        border-radius: 3px;
        color: #3344E5;
        opacity: 0.8;
      }
    }
  }
}

.admin {

  &__stats {
    display: flex;
    gap: 30px;
  }

  &__stat {
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;

    & >:last-child {
      color: #103FE3;
    }
  }
}

.admin-fund {

  &:not(:first-child) {
    margin-top: 40px;
  }

  &__title {
    font-size: 30px;
    margin-top: 10px;
  }

  &__sum {
    & td {
      border-top: 1px solid #020202;
      border-bottom: 1px solid #020202 !important;
      background: #ebedff;
      padding-top: 15px !important;
      padding-bottom: 15px !important;
    }
  }
}
